import styled from 'styled-components'

const HeaderTitle = ({ children, center = false }) => {
  return (
    <StyledHeaderTitle center={center}>{children}</StyledHeaderTitle>
  )
}

const StyledHeaderTitle = styled.h1`
  color: ${(props) => props.theme.colors.text.primary};
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 150%;

  ${(props) =>
    props.center
      ? `
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    font-size: 1rem;
    font-weight: 600;
  `
      : null}
`

export default HeaderTitle
