import {
  Header,
  HeaderBack,
  HeaderIcon,
  HeaderIconWrapper,
  HeaderSpacer,
  HeaderTitle,
} from 'components/common'
import { FiShare } from 'react-icons/fi'

const WorkArticleHeader = ({ title }) => {
  return (
    <Header>
      <HeaderBack />
      <HeaderTitle center>{title}</HeaderTitle>
      <HeaderSpacer />
      <HeaderIconWrapper>
        <HeaderIcon>
          <FiShare />
        </HeaderIcon>
      </HeaderIconWrapper>
    </Header>
  )
}

export default WorkArticleHeader
