import styled from 'styled-components'

const ListThumbnail = ({ src, alt }) => {
  return <Block>{src ? <Image src={src} alt={alt} /> : null}</Block>
}

const Block = styled.div`
  position: relative;
  width: 3rem;
  height: 3rem;
  border-radius: 12px;
  overflow: hidden;
`

const Image = styled.img`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
`

export default ListThumbnail
