import { css } from 'styled-components'
import colors from './colors'
import palette from './palette'

export const theme = {
  bp: {
    small: 414,
    medium: 768,
    large: 1024,
    xLarge: 1200,
    xxLarge: 1400,
    wide: 1600,
    xWide: 1920,
  },

  size: {
    headerHeight: 52,
    scrolledHeaderHeight: 54,
    navHeight: 62,
    containerPadding: 24,
    containerWidth: 684,
    // containerWidth: 768,
  },

  spacing: {
    50: '2px',
    100: '4px',
    300: '8px',
    400: '16px',
    500: '20px',
    600: '24px',
    700: '28px',
    800: '30px',
    900: '32px',
    1000: '40px',
    1100: '44px',
    1200: '48px',
    1300: '52px',
    1400: '56px',
  },

  zIndex: {
    header: 20,
    nav: 20,
  },

  shadow: {
    'button-1':
      'rgba(0, 0, 0, 0.05) 0 1px 1px, rgba(0, 0, 0, 0.1) 0 6px 20px',
    'button-2':
      'rgba(0, 0, 0, 0.1) 0 1px 1px, rgba(0, 0, 0, 0.25) 0 6px 20px',
    card: 'rgba(0, 0, 0, 0.05) 0 1px 2px, rgba(0, 0, 0, 0.05) 0 6px 32px',
    'card-1':
      'rgba(0, 0, 0, 0.05) 0 1px 2px, rgba(0, 0, 0, 0.05) 0 6px 32px',
    'card-2':
      'rgba(0, 0, 0, 0.1) 0 1px 2px, rgba(0, 0, 0, 0.1) 0 6px 32px',
  },

  borderRadius: {
    0: '4px',
    1: '6px',
    2: '8px',
    3: '16px',
  },

  palette,
}

// 위에있는 size 객체에 따라 자동으로 media 쿼리 함수를 만들어줍니다.
// 참고: https://www.styled-components.com/docs/advanced#media-templates
export const media = Object.keys(theme.bp).reduce((acc, label) => {
  acc[label] = (...args) => css`
    @media (max-width: ${(props) => props.theme.bp[label] / 16}rem) {
      ${css(...args)};
    }
  `

  return acc
}, {})

export const light = {
  scheme: 'light',
  ...theme,
  colors: {
    ...colors.light,
  },
}

export const dark = {
  scheme: 'dark',
  ...theme,
  colors: {
    ...colors.dark,
  },
}

dark.colors.base = {
  white: '#000000',
  black: '#ffffff',
}

dark.colors.gray = {
  900: '#F9FAFB',
  800: '#F2F4F6',
  700: '#E5E8EB',
  600: '#D1D6DB',
  500: '#B0B8C1',
  400: '#8B95A1',
  300: '#6B7684',
  200: '#4E5968',
  100: '#333D4B',
  50: '#191F28',
}

export default theme
