import { forwardRef } from 'react'
import styled, { css } from 'styled-components'
import { rgba } from 'polished'
import PropTypes from 'prop-types'

const Button = forwardRef(
  (
    {
      as,
      type = 'button',
      children,
      onClick,
      style,
      color,
      size,
      expand,
      alignLeft,
      alignRight,
      plain = false,
      border = false,
      round = false,
      circle = false,
      disabled = false,
      href,
      target,
    },
    ref,
  ) => (
    <StyledButton
      as={as}
      ref={ref}
      type={type}
      style={style}
      onClick={onClick}
      buttonColor={color}
      plain={plain}
      border={border}
      expand={expand}
      size={size}
      alignLeft={alignLeft ? true : false}
      alignRight={alignRight ? true : false}
      round={round}
      circle={circle}
      disabled={disabled}
      href={href}
      target={target}
    >
      {children}
    </StyledButton>
  ),
)

const getBorderButton = (color) => css`
  color: ${color};
  background-color: transparent;
  border-color: ${color};

  &:hover {
    color: ${color};
    background-color: ${rgba(color, 0.1)};
    border-color: ${color};
  }
  &:active {
    color: ${color};
    background-color: ${rgba(color, 0.2)};
    border-color: ${color};
  }
`
const getPlainButton = (color) => css`
  color: ${color};
  background-color: ${rgba(color, 0.1)};
  border-color: transparent;

  &:hover {
    color: ${color};
    background-color: ${rgba(color, 0.2)};
    border-color: transparent;
  }
  &:active {
    color: ${color};
    background-color: ${rgba(color, 0.3)};
    border-color: transparent;
  }
`

const colors = {
  default: css`
    color: ${(props) => props.theme.colors.gray[800]};
    font-weight: 600;
    background-color: ${(props) =>
      props.theme.colors.background.elevated};
    border-color: ${(props) => props.theme.colors.gray[200]};

    i {
      color: ${(props) => props.theme.colors.gray[500]};
    }

    &:hover {
      background-color: ${(props) => props.theme.colors.gray[50]};
      border-color: ${(props) => props.theme.colors.gray[300]};
    }
    &:active {
      background-color: ${(props) => props.theme.colors.gray[100]};
      border-color: ${(props) => props.theme.colors.gray[300]};
    }
  `,
  gray: css`
    color: ${(props) => props.theme.colors.gray[800]};
    font-weight: 700;
    background-color: ${(props) => props.theme.colors.gray[100]};
    border-color: ${(props) => props.theme.colors.gray[100]};

    &:hover {
      background-color: ${(props) => props.theme.colors.gray[200]};
      border-color: ${(props) => props.theme.colors.gray[200]};
    }
    &:active {
      background-color: ${(props) => props.theme.colors.gray[300]};
      border-color: ${(props) => props.theme.colors.gray[300]};
    }
  `,
  darkGray: css`
    color: ${(props) => props.theme.colors.gray[800]};
    background-color: ${(props) => props.theme.colors.gray[200]};
    border-color: ${(props) => props.theme.colors.gray[200]};

    &:hover {
      background-color: ${(props) => props.theme.colors.gray[300]};
      border-color: ${(props) => props.theme.colors.gray[300]};
    }
    &:active {
      background-color: ${(props) => props.theme.colors.gray[400]};
      border-color: ${(props) => props.theme.colors.gray[400]};
    }
  `,
  black: css`
    color: white;
    background-color: ${(props) => props.theme.colors.gray[900]};
    border-color: ${(props) => props.theme.colors.gray[900]};

    ${({ border }) => border && getBorderButton(theme.colors.gray[900])}
    ${({ plain }) => plain && getPlainButton(theme.colors.gray[900])}
  `,
  primary: css`
    color: white;
    background-color: ${(props) => props.theme.colors.primary[400]};
    border-color: ${(props) => props.theme.colors.primary[400]};

    ${({ border }) =>
      border && getBorderButton(theme.colors.primary[400])}

    ${({ plain }) => plain && getPlainButton(theme.colors.primary[400])}
  `,
  danger: css`
    color: white;
    background-color: ${(props) => props.theme.colors.tint.danger};
    border-color: ${(props) => props.theme.colors.tint.danger};

    ${({ border }) =>
      border && getBorderButton(theme.colors.tint.danger)}

    ${({ plain }) => plain && getPlainButton(theme.colors.tint.danger)}
  `,
}

const plainColors = {
  primary: css`
    color: ${(props) => props.theme.colors.primary[500]};
    background-color: ${(props) => props.theme.colors.primary[50]};
    border-color: ${(props) => props.theme.colors.primary[50]};

    &:hover {
      background-color: ${(props) => props.theme.colors.primary[100]};
      border-color: ${(props) => props.theme.colors.primary[100]};
    }
    &:active {
      background-color: ${(props) => props.theme.colors.primary[200]};
      border-color: ${(props) => props.theme.colors.primary[200]};
    }
  `,
}

const buttonHeights = {
  xlarge: 52,
  large: 42,
  medium: 38, // default
  small: 30,
}
const buttonSizes = {
  xlarge: css`
    font-size: 1rem;
    height: ${buttonHeights['xlarge']}px;
    padding-left: 1.5rem;
    padding-right: 1.5rem;

    ${({ expand }) =>
      expand &&
      `
      padding-left: 1.25rem;
      padding-right: 1.25rem;
    `}
  `,
  large: css`
    height: ${buttonHeights['large']}px;
    padding-left: 1rem;
    padding-right: 1rem;
  `,
  medium: css`
    height: ${buttonHeights['medium']}px;
  `,
  small: css`
    font-size: 0.75rem;
    font-weight: 600;
    height: ${buttonHeights['small']}px;
    padding-left: 0.75rem;
    padding-right: 0.75rem;
  `,
}

const StyledButton = styled.button`
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;

  padding: 0 1rem;

  font-size: 0.875rem;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  text-decoration: none;
  appearance: none;
  border: 1px solid ${(props) => props.theme.colors.gray[200]};
  border-radius: ${(props) => props.theme.borderRadius[2]};

  outline: none;
  appearance: none;

  transition: 0.2s ease;

  ${({ alignLeft }) =>
    alignLeft &&
    `
    justify-content: flex-start;
  `}
  ${({ alignRight }) =>
    alignRight &&
    `
    justify-content: flex-end;
  `}

  svg {
    flex: 0 0 auto;
    max-width: 1.25rem;
    max-height: 1.25rem;
  }

  & > i,
  & > svg {
    & + span {
      margin-left: 0.75em;
    }
  }

  & > span + i,
  & > span + svg {
    margin-left: 0.75em;
  }

  ${({ buttonColor }) => colors[buttonColor || 'default']}
  ${({ size }) => buttonSizes[size || 'medium']}

  ${({ expand }) =>
    expand &&
    `
    width: 100%;
  `}

  ${({ round }) =>
    round &&
    `
      border-radius: 4rem;
  `}

  ${({ circle, size }) =>
    circle &&
    `
      border-radius: 4rem;

      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: ${size ? buttonHeights[size] : buttonHeights['medium']}px;
      padding: 0 !important;

      & > i {
        font-size: 1rem;
      }
      & > svg {
        width: 1.25rem;
        height: 1.25rem;
      }
  `}

  ${({ disabled }) =>
    disabled &&
    `
    cursor: not-allowed;
    pointer-events: none;

    color: ${(props) => props.theme.colors.gray[400]};
  `}
`

Button.propTypes = {
  type: PropTypes.oneOf(['button', 'submit']),
  color: PropTypes.oneOf([
    'default',
    'gray',
    'darkGray',
    'black',
    'primary',
    'danger',
  ]),
  // plain: PropTypes.bool,
  size: PropTypes.oneOf(['small', 'medium', 'large', 'xlarge']),
  // onClick: PropTypes.func,
  // expand: PropTypes.bool,
  // disabled: PropTypes.bool,
  // circle: PropTypes.bool,
}

Button.defaultProps = {
  type: 'button',
  color: 'default',
  // plain: false,
  size: 'medium',
  // onClick: undefined,
  // expand: false,
  // disabled: false,
  // circle: false,
}

export default Button
