import styled from 'styled-components'

const Tag = ({ children }) => {
  return <Block>{children}</Block>
}

const Block = styled.span`
  padding: 2px 6px;
  color: ${(props) => props.theme.colors.gray[500]};
  font-size: 0.75rem;
  font-weight: 600;
  background-color: ${(props) => props.theme.colors.gray[100]};
  border-radius: 4px;
`

export default Tag
