import {
  ListDesc,
  ListItem,
  ListThumbnail,
  ListTitle,
} from 'components/common'
import categoriesData from 'data/categoriesData'

const WorkListItem = ({ item }) => {
  const getCategoryNameById = (id) => {
    const findData = categoriesData.find((e) => e.id === id)
    if (!findData) return null
    return findData.name
  }

  return (
    <ListItem
      to={`/works/${item.id}`}
      left={<ListThumbnail src={item.thumbnail} alt={item.title} />}
    >
      <ListTitle>{item.title}</ListTitle>
      <ListDesc>
        {item?.category.map((categoryId, index) => {
          if (index + 1 === item?.category.length)
            return getCategoryNameById(categoryId)
          return `${getCategoryNameById(categoryId)} · `
        })}
      </ListDesc>
    </ListItem>
  )
}

export default WorkListItem
