import styled from 'styled-components'

const HomeSection = ({ title, children, paddingBottom = '2rem' }) => {
  return (
    <Wrapper paddingBottom={paddingBottom}>
      <Title>{title}</Title>
      <Body>{children}</Body>
    </Wrapper>
  )
}

const Wrapper = styled.section`
  padding-left: ${(props) => props.theme.size.containerPadding}px;
  padding-right: ${(props) => props.theme.size.containerPadding}px;
  padding-top: 1.75rem;
  ${({ paddingBottom }) =>
    paddingBottom ? `padding-bottom: ${paddingBottom};` : null}
`
const Title = styled.h2`
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 150%;
  margin-bottom: 1.5rem;
`
const Body = styled.div``

export default HomeSection
