import palette from './palette'

const light = {
  ...palette,
}

light.text = {}
light.text.primary = palette.gray[900]
light.text.secondary = palette.gray[500]
light.text.placeholder = palette.gray[400]
light.text.disabled = palette.gray[400]
light.text.highlight = palette.primary[400]
light.text.info = palette.blue[400]
light.text.danger = palette.red[400]

light.background = {}
light.background.base = palette.gray[100]
light.background.elevated = palette.base.white
light.background.hover = palette.gray[50]

light.border = {}
light.border.thick = palette.gray[300]
light.border.basic = palette.gray[200]
light.border.thin = palette.gray[100]

// ────────────────────────────────────────────────────

const dark = {
  ...palette,
  // gray: {
  //   900: '#F9FAFB',
  //   800: '#F2F4F6',
  //   700: '#E5E8EB',
  //   600: '#D1D6DB',
  //   500: '#B0B8C1',
  //   400: '#8B95A1',
  //   300: '#6B7684',
  //   200: '#4E5968',
  //   100: '#333D4B',
  //   50: '#191F28',
  // },
}
dark.text = { ...light.text }
dark.background = { ...light.background }
dark.border = { ...light.border }

dark.text.primary = palette.base.white

// dark.background.base = palette.gray[800]
dark.background.base = '#13181d'
dark.background.elevated = palette.gray[900]
dark.background.hover = palette.gray[800]

dark.border.basic = palette.gray[800]
dark.border.thin = palette.gray[800]
// dark.border.basic = '#13181d'
// dark.border.thin = '#13181d'

const colors = {
  light,
  dark,
}

export default colors
