import { createSlice } from '@reduxjs/toolkit'

const initialState = {
  themeScheme: 'light',
  isScrolled: '',
}

export const baseSlice = createSlice({
  name: 'base',
  initialState,
  reducers: {
    setThemeScheme(state, actions) {
      state.themeScheme = actions.payload
    },
    setIsScrolled(state, actions) {
      state.isScrolled = actions.payload
    },
  },
})

export const { setThemeScheme, setIsScrolled } = baseSlice.actions

export default baseSlice.reducer
