import { useEffect } from 'react'

import theme from 'lib/styles/theme'
import styled from 'styled-components'
import Container from './Container'
import { useDispatch } from 'react-redux'
import { setIsScrolled } from 'modules/base'
import Nav from './Nav'
import Footer from './Footer'

const Layout = ({ header, navHidden, children }) => {
  const dispatch = useDispatch()

  useEffect(() => {
    const handleScroll = (e) => {
      const value = window.scrollY
      if (value <= 0) {
        dispatch(setIsScrolled(false))
        return
      }
      dispatch(setIsScrolled(true))
    }

    window.addEventListener('scroll', handleScroll)

    return () => {
      window.removeEventListener('scroll', handleScroll)
    }
  }, [])

  return (
    <Main header={header}>
      {header}
      {children}
      {/* <Container>{children}</Container> */}
      <Footer />
      {navHidden ?? <Nav />}
    </Main>
  )
}

const Main = styled.main`
  min-height: 100vh;
  max-width: ${(props) => props.theme.size.containerWidth}px;
  ${(props) =>
    props.header
      ? `padding-top: ${props.theme.size.headerHeight}px;`
      : null}
  padding-bottom: ${(props) => props.theme.size.navHeight}px;
  margin: 0 auto;
  background-color: ${(props) =>
    props.theme.colors.background.elevated};
  overflow-x: hidden;
`

export default Layout
