import styled from 'styled-components'
import { rgba } from 'polished'

const HomeBlogItem = ({
  thumbnail,
  category,
  title,
  description,
  color,
  href,
}) => {
  return (
    <Li>
      <Item color={color} href={href} target="_blank">
        <ItemCategory color={color}>{category}</ItemCategory>

        <Content>
          <Body>
            <ItemTitle>{title}</ItemTitle>
            <ItemDesc>{description}</ItemDesc>
          </Body>

          <Thumbnail>
            {thumbnail && <img src={thumbnail} alt={title} />}
          </Thumbnail>
        </Content>
      </Item>
    </Li>
  )
}

const Li = styled.li`
  &:not(:last-child) {
    margin-right: 1rem;
  }
`
const Item = styled.a`
  display: block;
  width: 280px;
  padding: 1rem;
  border-radius: 12px;
  background-color: ${(props) =>
    props.color
      ? rgba(props.color, 0.1)
      : rgba(props.theme.colors.primary[400], 0.1)};

  text-decoration: none;
`

const Content = styled.div`
  display: flex;
  align-items: center;
`
const Body = styled.div`
  flex: 1;
  padding-right: 1rem;
`
const Thumbnail = styled.div`
  position: relative;
  flex: 0 0 auto;
  width: 64px;
  height: 64px;

  border-radius: 8px;
  background-color: ${(props) => props.theme.colors.gray[200]};
  overflow: hidden;

  & > img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
`

const ItemCategory = styled.div`
  display: inline-block;
  padding: 3px 10px;
  margin-bottom: 0.75rem;

  color: white;
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 150%;

  background-color: ${(props) =>
    props.color ? props.color : props.theme.colors.primary[400]};
  border-radius: 50px;
`
const ItemTitle = styled.h3`
  color: ${(props) => props.theme.colors.text.primary};
  font-size: 1rem;
  font-weight: 700;
  line-height: 150%;
  margin-bottom: 0.25rem;

  word-break: break-word;
  overflow-wrap: break-word;

  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
`
const ItemDesc = styled.div`
  color: ${(props) => props.theme.colors.text.secondary};

  word-break: break-word;
  overflow-wrap: break-word;
  font-size: 0.75rem;
  line-height: 1.5;
  height: calc(0.75rem * 1.5 * 1);

  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
`

export default HomeBlogItem
