import { ThemeProvider } from 'styled-components'
import { useSelector } from 'react-redux'
import { light, dark } from 'lib/styles/theme'
import { useEffect, useState } from 'react'
import useThemeScheme from 'lib/hooks/useThemeScheme'

const ThemeCore = ({ children }) => {
  // const themeScheme = useSelector((state) => state.base.themeScheme)
  const { themeScheme, setTheme } = useThemeScheme()
  const [success, setSuccess] = useState(false)

  useEffect(() => {
    const savedThemeScheme = localStorage.getItem('themeScheme')
    console.log(savedThemeScheme)

    if (!savedThemeScheme) {
      setSuccess(true)
      return
    }

    setTheme(savedThemeScheme)
    setSuccess(true)
  }, [])

  return (
    <ThemeProvider theme={themeScheme === 'light' ? light : dark}>
      {success ? children : null}
    </ThemeProvider>
  )
}

export default ThemeCore
