import Container from 'components/base/Container'
import Layout from 'components/base/Layout'
import { Divider, Spacing, Tab, TabItem } from 'components/common'
import HomeSection from 'components/home/HomeSection'
import WorkArticleHeader from 'components/work/WorkArticleHeader'
import WorkArticleSummary from 'components/work/WorkArticleSummary'
import WorkArticleThumbnail from 'components/work/WorkArticleThumbnail'
import worksData from 'data/worksData'
import useScrollRestoration from 'lib/hooks/useScrollRestoration'
import { useState } from 'react'
import { useEffect } from 'react'

const WorkArticlePage = ({ match }) => {
  const { id } = match.params
  const works = worksData
  const [data, setData] = useState(null)

  useScrollRestoration()

  useEffect(() => {
    const getWorkData = works.find((e) => e.id === id)
    setData(getWorkData)
  }, [id])

  return (
    <Layout header={<WorkArticleHeader title={data?.title} />}>
      <WorkArticleThumbnail
        src={data?.thumbnail}
        alt={data?.title ?? 'thumbnail'}
      />
      <WorkArticleSummary data={data} />

      <Spacing />
      <Divider />

      {data?.content ? (
        <HomeSection title="설명">{data?.content}</HomeSection>
      ) : null}
    </Layout>
  )
}

export default WorkArticlePage
