import categoriesData from 'data/categoriesData'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

const HomePortfolioItem = ({ item }) => {
  const getCategoryNameById = (id) => {
    const findData = categoriesData.find((e) => e.id === id)
    if (!findData) return null
    return findData.name
  }

  return (
    <Li>
      <Item to={`/works/${item.id}`}>
        <Thumbnail>
          {item?.thumbnail && (
            <img src={item?.thumbnail} alt={item?.title} />
          )}
        </Thumbnail>
        <Body>
          <Title>{item?.title}</Title>
          <Meta>
            {item?.category.map((categoryId, index) => {
              if (index + 1 === item?.category.length)
                return getCategoryNameById(categoryId)
              return `${getCategoryNameById(categoryId)} · `
            })}
          </Meta>
        </Body>
      </Item>
    </Li>
  )
}

const Li = styled.li`
  flex-basis: 50%;
  max-width: 50%;
  padding: 0.5rem;
`

const Item = styled(Link)`
  display: block;
  height: 100%;
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid ${(props) => props.theme.colors.border.basic};
  box-shadow: ${(props) => props.theme.shadow['card-1']};

  color: ${(props) => props.theme.colors.text.primary};
  text-decoration: none;
`

const Thumbnail = styled.div`
  position: relative;
  padding-bottom: 62%;
  background-color: ${(props) => props.theme.colors.gray[200]};
  overflow: hidden;
  /* border-radius: 12px; */
  /* margin-bottom: 0.75rem; */

  & > img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
`

const Body = styled.div`
  padding: 1rem;
`

const Title = styled.div`
  font-size: 1rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
`
const Meta = styled.div`
  color: ${(props) => props.theme.colors.text.secondary};
  font-size: 0.75rem;
`

export default HomePortfolioItem
