import styled from 'styled-components'
import categoriesData from 'data/categoriesData'
import { Button, ListDesc, Spacing, Tag } from 'components/common'
import Container from 'components/base/Container'
import { FcHome, FcOvertime } from 'react-icons/fc'

const WorkArticleSummary = ({ data }) => {
  const getCategoryNameById = (id) => {
    const findData = categoriesData.find((e) => e.id === id)
    if (!findData) return null
    return findData.name
  }

  return (
    <Block>
      <Container>
        <TagWrap>
          {data?.category.map((categoryId, index) => (
            <Tag key={index}>{getCategoryNameById(categoryId)}</Tag>
          ))}
        </TagWrap>

        <Title>{data?.title}</Title>
        <ListDesc>
          {data?.skills.map((item, index) => {
            if (index + 1 === data?.skills.length) return item
            return `${item} · `
          })}
        </ListDesc>

        <Spacing size={700} />

        {data?.period ? (
          <MetaItem>
            <FcOvertime />
            <span>{data?.period}</span>
          </MetaItem>
        ) : null}

        {data?.links.map((item, key) => (
          <MetaItem key={key}>
            <FcHome />
            <a href={item} target="_blank">
              {item}
            </a>
          </MetaItem>
        ))}

        <Spacing size="300" />

        {data?.links[0] ? (
          <Button
            color="primary"
            as="a"
            href={data?.links[0]}
            target="_blank"
            expand
          >
            보러가기
          </Button>
        ) : null}
      </Container>
    </Block>
  )
}

const Block = styled.div`
  padding-top: 1.5rem;
  padding-bottom: 0.5rem;
`

const TagWrap = styled.div`
  display: flex;
  margin-bottom: 0.75rem;
  & > span {
    margin-right: 0.25rem;
  }
`

const Title = styled.div`
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
`
const MetaItem = styled.div`
  display: flex;
  align-items: flex-start;
  padding-bottom: 0.875rem;
  margin-bottom: 0.875rem;

  color: ${(props) => props.theme.colors.text.primary};
  font-size: 0.875rem;

  border-bottom: 1px solid ${(props) => props.theme.colors.border.thin};

  & > svg {
    color: ${(props) => props.theme.colors.gray[400]};
    font-size: 1.25rem;
    margin-top: 1px;
    margin-right: 0.5rem;
  }

  & > a {
    color: ${(props) => props.theme.colors.text.primary};
    &:hover {
      color: ${(props) => props.theme.colors.gray[600]};
    }
  }
`

export default WorkArticleSummary
