import useThemeScheme from 'lib/hooks/useThemeScheme'
import { rgba } from 'polished'
import { FileText, Grid, GitHub } from 'react-feather'
import { FiGithub } from 'react-icons/fi'
import styled from 'styled-components'

const HomeQuickMenu = () => {
  const { getTheme } = useThemeScheme()
  const theme = getTheme()

  return (
    <Wrapper>
      <HomeQuickMenuItem
        color={theme.colors.primary[400]}
        icon={<FileText />}
        text="블로그"
        href="https://calvinsnax.com/blog"
        isBlank
      />
      <HomeQuickMenuItem
        color={theme.colors.red[400]}
        icon={<Grid />}
        text="포트폴리오"
        href="/works"
      />
      <HomeQuickMenuItem
        color={theme.colors.gray[700]}
        icon={<GitHub />}
        href="https://github.com/calvinsnax"
        text="Github"
        isBlank
      />
    </Wrapper>
  )
}

const Wrapper = styled.ul`
  display: flex;
  align-items: center;
  padding-bottom: 2rem;
`

const HomeQuickMenuItem = ({
  color,
  icon,
  text,
  href,
  isBlank = false,
}) => {
  return (
    <Li>
      <Item href={href} target={isBlank ? '_blank' : '_self'}>
        <ItemImage color={color}>{icon}</ItemImage>
        <ItemText>{text}</ItemText>
      </Item>
    </Li>
  )
}

const Li = styled.li`
  margin-right: 14px;
`

const Item = styled.a`
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;

  color: ${(props) => props.theme.colors.text.primary};
  font-size: 13px;
  font-weight: 600;
  line-height: 150%;
  text-decoration: none;
  appearance: none;
`
const ItemImage = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  margin-bottom: 5px;
  border-radius: 100%;

  font-size: 1.25rem;

  ${({ color, theme }) =>
    color
      ? `
        color: ${color};
        background-color: ${rgba(color, 0.1)};
      `
      : `
      color: ${theme.colors.primary[400]};
      background-color: ${theme.colors.primary[50]};
  `}

  & > img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
`
const ItemText = styled.span``

export default HomeQuickMenu
