import { rgba } from 'polished'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

const ListItem = ({
  children,
  left,
  right,
  as,
  to,
  href,
  target,
  onClick,
}) => {
  return (
    <Li isHover={to || href || onClick ? true : false}>
      <Item
        as={to ? Link : 'a'}
        to={to}
        href={href}
        target={target}
        onClick={onClick}
      >
        <Left>{left}</Left>
        <Content>{children}</Content>
        <Right>{right}</Right>
      </Item>
    </Li>
  )
}

const Li = styled.li`
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    margin-left: ${(props) => props.theme.size.containerPadding}px;
    width: calc(
      100% - ${(props) => props.theme.size.containerPadding}px
    );
    border-bottom: 1px solid
      ${(props) => props.theme.colors.border.basic};
  }

  ${(props) => {
    if (!props.isHover) return

    return `
      transition: .2s ease;
      &:hover {
        background-color: ${
          props.theme.scheme === 'light'
            ? props.theme.colors.gray[50]
            : rgba(props.theme.colors.gray[100], 0.5)
        };
      }
    `
  }}
`

const Item = styled(Link)`
  display: flex;
  padding: 1.5rem ${(props) => props.theme.size.containerPadding}px;
  color: ${(props) => props.theme.colors.text.primary};
  text-decoration: none;
`

const Left = styled.div`
  margin-right: 1rem;
`
const Content = styled.div`
  flex: 1;
`
const Right = styled.div`
  margin-left: 1rem;
`

export default ListItem
