import { rgba } from 'polished'
import styled from 'styled-components'
import theme from 'lib/styles/theme'
import { useSelector } from 'react-redux'
import FixedBlcok from 'components/base/FixedBlock'

const Header = ({ children }) => {
  const isScrolled = useSelector((state) => state.base.isScrolled)

  return (
    <>
      <Fixed>
        <Wrapper isScrolled={isScrolled}>{children}</Wrapper>
      </Fixed>
      {/* <HeaderSpacer /> */}
    </>
  )
}

const Fixed = styled(FixedBlcok)`
  position: fixed;
  top: 0;
  left: 0;
  z-index: ${(props) => props.theme.zIndex.header};
  user-select: none;
`

// const HeaderSpacer = styled.div`
//   height: ${(props) => props.theme.size.headerHeight}px;
// `

const Wrapper = styled.header`
  display: flex;
  align-items: center;
  max-width: ${(props) => props.theme.size.containerWidth}px;
  width: 100%;
  height: ${(props) => props.theme.size.headerHeight}px;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  margin: 0 auto;

  background: rgba(255, 255, 255, 0.9);
  background-color: ${(props) =>
    rgba(props.theme.colors.background.elevated, 0.9)};
  backdrop-filter: blur(18px);

  transition: 0.2s ease height, 0.3s ease box-shadow;

  ${({ isScrolled }) =>
    isScrolled &&
    `
    height: ${(props) => props.theme.size.scrolledHeaderHeight}px;
    box-shadow: 0 1px ${(props) =>
      rgba(props.theme.colors.base.black, 0.1)};
  `}
`

export default Header
