import theme from 'lib/styles/theme'
import styled from 'styled-components'
import FixedBlock from './FixedBlock'
import NavItem from './NavItem'

import { HiViewGrid, HiUser } from 'react-icons/hi'
import { AiFillHome, AiFillSchedule } from 'react-icons/ai'

const Nav = () => {
  return (
    <Fixed>
      <Wrapper>
        <NavItem to="/" exact icon={<AiFillHome />} text="홈" />
        <NavItem to="/works" icon={<HiViewGrid />} text="포트폴리오" />
        <NavItem to="/about" icon={<AiFillSchedule />} text="소개" />
        <NavItem to="/profile" icon={<HiUser />} text="프로필" />
      </Wrapper>
    </Fixed>
  )
}

const Fixed = styled(FixedBlock)`
  top: auto;
  bottom: 0;
  left: 0;
  z-index: ${(props) => props.theme.zIndex.nav};
`

const Wrapper = styled.div`
  display: flex;
  align-items: center;
  max-width: ${(props) => props.theme.size.containerWidth}px;
  height: ${(props) => props.theme.size.navHeight}px;
  height: calc(
    ${(props) => props.theme.size.navHeight}px +
      env(safe-area-inset-bottom)
  );
  padding-bottom: env(safe-area-inset-bottom);
  margin: 0 auto;

  background-color: ${(props) =>
    props.theme.colors.background.elevated};
  border-top: 1px solid ${(props) => props.theme.colors.border.basic};
`

export default Nav
