import { useDispatch, useSelector } from 'react-redux'
import { setThemeScheme } from 'modules/base'
import { light, dark } from 'lib/styles/theme'

export default function useThemeScheme() {
  const dispatch = useDispatch()
  const themeScheme = useSelector((state) => state.base.themeScheme)

  const setTheme = (payload) => {
    dispatch(setThemeScheme(payload))
    localStorage.setItem('themeScheme', payload)
  }

  const toggleTheme = () => {
    if (themeScheme === 'light') {
      setTheme('dark')
      return
    }
    setTheme('light')
  }

  const getTheme = () => {
    return themeScheme === 'light' ? light : dark
  }

  return {
    themeScheme,
    setTheme,
    toggleTheme,
    getTheme,
  }
}
