import styled from 'styled-components'

const WorkArticleThumbnail = ({ src, alt }) => {
  return (
    <Block>
      <Image src={src} alt={alt} />
    </Block>
  )
}

const Block = styled.div`
  position: relative;
  padding-bottom: 50%;
`

const Image = styled.img`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
`

export default WorkArticleThumbnail
