import styled from 'styled-components'

const Tab = ({ children }) => {
  return <Block>{children}</Block>
}

const Block = styled.ul`
  list-style: none;
  display: flex;
  align-items: center;
  padding: 0 ${(props) => props.theme.size.containerPadding}px;
  margin: 0;
  height: 3rem;
  border-bottom: 1px solid ${(props) => props.theme.colors.border.basic};
  overflow-x: auto;
`

export default Tab
